
html,
body {
  height: 100%;
  margin-top: 0px;
  overflow: hidden !important;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 100%;
}
#app .logoImg {
    height: 200px;
    -webkit-transition: all .3s;
    transition: all .3s;
}
#app .normal {
    margin-top: -200px;
}

.mainPanel {
  width: 70%;
  margin: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  position: relative;
}
.mainPanel .live2dFrame .header {
    font-size: 16px;
}

.l2dPanel {
  width: 100%;
  height: 100%;
}
#landlord {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    bottom: 0;
    width: 280px;
    height: 450px;
    z-index: 10000;
    font-size: 0;
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}

#live2d {
    position: relative;
}

.message {
    opacity: 0;
    width: 300px;
    height: auto;
    margin: auto;
    padding: 7px;
    top: -80px;
    left: -10px;
    text-align: center;
    border: 1px solid rgba(255,137,255,.4);
    border-radius: 12px;
    background-color: rgba(255,137,255,.2);
    -webkit-box-shadow: 0 3px 15px 2px rgba(255,137,255,.4);
            box-shadow: 0 3px 15px 2px rgba(255,137,255,.4);
    font-size: 13px;
    font-weight: 400;
    text-overflow: ellipsis;
    text-transform: uppercase;
    overflow: hidden;
    position: absolute;
    -webkit-animation-delay: 5s;
            animation-delay: 5s;
    -webkit-animation-duration: 50s;
            animation-duration: 50s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-name: shake;
            animation-name: shake;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}

.hide-button {
    position: absolute;
    top: 10px;
    right: 0;
    /* bottom: 30px; */
    display: none;
    overflow: hidden;
    /* padding: 4px; */
    width: 60px;
    height: 20px;
    border: 1px solid rgba(255,137,255,.4);
    border-radius: 12px;
    background: rgba(255,137,255,.2);
    -webkit-box-shadow: 0 3px 15px 2px rgba(255,137,255,.4);
            box-shadow: 0 3px 15px 2px rgba(255,137,255,.4);
    text-align: center;
    font-size: 12px;
    cursor: pointer;
}

.hide-button:hover {
    border: 1px solid #f4a7b9;
    background: #f4f6f8;
}

@media (max-width: 860px) {
    #landlord {
        display: none;
    }
}

